.page-dashboard {
  height: 100%;
}

.page-dashboard .nav {
  width: 100%;
  padding: 4px 10px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.page-dashboard .nav .brand {
  color: rgba(0, 0, 0, 0.3) !important;
}

.page-dashboard .nav .burger-menu {
  display: none;
}

.page-dashboard .container {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.page-dashboard aside {
  flex: 0.2;
  border-inline-end: 1px solid rgba(5, 5, 5, 0.06);
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 4px;
}

.page-dashboard aside .brand {
  margin: 30px 0;
  padding: 0 5%;
}

.page-dashboard aside .brand a {
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 4px;
  border-radius: 10px;
}

.brand a {
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 10px;
}

.brand a svg {
  color: rgba(0, 0, 0, 0.7);
}

.brand a img {
  height: 35px;
}

.brand a span {
  font-size: 22px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  color: #000000cc;
  padding-left: 10px;
}

.page-dashboard div.main {
  flex: 0.8;
  position: relative;
  overflow-x: hidden;
}

@media screen and (max-width: 768px) {
  .page-dashboard aside {
    display: none;
  }
  .page-dashboard div.main {
    flex: 1;
  }
  .page-dashboard .nav .burger-menu {
    display: block;
  }
}
