.tiptap > * + * {
  margin-top: 0.75em;
}

.tiptap ul,
.tiptap ol {
  padding: 0 1rem;
}

.richeditor {
  position: relative;
  background-color: #fff;
  transition: all 0.5s ease;
  height: 100%;
  overflow: scroll;
}

.richeditor.expanded {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 80;
  width: 100%;
  height: 100%;
}

.richeditor.expanded .richeditor-toolbar {
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  width: 100%;
}

.richeditor-toolbar {
  position: sticky;
  top: 0px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  padding: 5px;
  border-radius: 7px;
  margin: 5px 0;
  background-color: #fff;
  z-index: 50;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.richeditor-contents {
  height: 100%;
  min-height: 110vh;
  outline: none;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
  padding: 5px 20px 0;
}

.tiptap > * + * {
  margin-top: 0.75em;
}

.tiptap blockquote {
  border-left: 3px solid rgba(13, 13, 13, 0.1);
  padding-left: 1rem;
}
