.home-page header {
  width: 100%;
  height: 300px;
  background-color: #8ec5fc;
  background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Courier New", Courier, monospace;
  text-align: center;
}

.home-page header h1.title {
  font-size: 4em;
  text-decoration: underline;
}

.home-page header p.subtitle {
  font-size: 16px;
  padding-top: 10px;
}

main {
}

main div.home-posts-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
  padding: 10px;
}

main div.home-posts-list > a {
  text-decoration: none;
  color: #333; /* Adjust the color as needed */
}

main div.home-posts-list .home-post-item {
  border-radius: 5px;
  background-color: #fff; /* Example background color */
  min-height: 300px;
}

.home-post-item .card-details {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
