.range-input-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.range-input-container label {
  text-wrap: nowrap;
  margin-right: 5px;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  color: #00000085;
  margin-bottom: 7px;
  display: flex;
  align-items: center;
}

.range-input-container label .name {
  font-size: 14px;
}

.range-input-container label span {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #494949ca;
  margin-right: 5px;
}

.range-input-container label .icon {
  color: #494949ca;
  font-weight: 600;
  cursor: help;
}

.range-inputs {
  display: flex;
}

.min {
  margin-right: 5px;
}

.max {
  margin-left: 5px;
}
