.table-filter-items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  background-color: #fff;
  box-shadow: rgba(247, 204, 204, 0.16) 0px 1px 4px;
  padding: 20px 10px;
  row-gap: 30px;
  column-gap: 40px;
}

.table-filter-item {
  display: flex;
  align-items: center;
}
