.select-input-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.select-input-container label {
  margin-bottom: 7px;
  display: flex;
  align-items: center;
}

.select-input-container label span {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #494949ca;
  margin-right: 5px;
}

.select-input-container label .icon {
  color: #494949ca;
  font-weight: 600;
  cursor: help;
}
