.mobile-table-container {
  background: #fff;
}
.mobile-table-container table {
  border-collapse: collapse;
  width: 100%;
  position: relative;
  background: #efeded;
}

.mobile-table-container table thead {
  position: sticky;
  top: 0;
  z-index: 70;
}

.mobile-table-container table thead tr {
  display: flex;
  flex-direction: row;
}

.mobile-table-container table thead tr th {
  background: #2b3b6e;
  flex: 1;
  color: #fff;
  border: none;
}

.mobile-table-container table td {
  border-bottom: 1px solid rgb(197, 197, 197);
}

.mobile-table-container table th,
.mobile-table-container table td {
}

.mobile-table-container .table-select {
  height: 100%;
  padding: 25px 15px;
  display: grid;
  grid-template-columns: 80% 10%;
  align-items: center;
  cursor: pointer;
  position: relative;
}

.mobile-table-container .table-select .name {
  font-size: 16px !important;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  color: inherit;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  text-wrap: nowrap;
}

.mobile-table-container .table-select .icon {
  position: absolute;
  right: 10px;
}

.mobile-table-container table thead .list {
  width: 100%;
  background: #ffffff;
  height: 300px;
  overflow: scroll;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.mobile-table-container table thead .list ul {
  padding: 10px;
  display: grid;
}

.mobile-table-container table thead .list ul li {
  list-style: none;
  padding: 15px 10px;
  margin-top: 10px;
  border: 1px solid grey;
  width: 100%;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #555;
  border: 1px solid #c6cbd4;
  border-radius: 5px;
  background: none;
}
.mobile-table-container table thead .list ul li:hover {
  background-color: #f5efef9f;
}

.mobile-table-container table thead .list ul li.selected {
  border: 1px solid #38d39f;
}
.mobile-table-container table thead .list ul li.disabled {
  border: 1px solid #c6cbd4;
  opacity: 0.4;
  cursor: default;
}

.mobile-table-container table thead .list ul li button {
  background: none;
  width: 100%;
  height: 100%;
}

.mobile-table-container table tbody tr td {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 18px;
}

.mobile-table-container table tbody tr td div.feature-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0;
}

.mobile-table-container table tbody tr td div.feature-container label {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  margin: 15px 0;
}

.mobile-table-container
  table
  tbody
  tr
  td
  div.feature-container
  .feature-values {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.mobile-table-container
  table
  tbody
  tr
  td
  div.feature-container
  .feature-values
  span {
  flex: 1;
  text-align: center;
  line-height: 50px;
  position: relative;
}

.mobile-table-container
  table
  tbody
  tr
  td
  div.feature-container
  .feature-values
  span:not(:first-child)::before {
  content: "";
  width: 1px;
  height: 100%;
  background: #efeded;
  position: absolute;
  left: 0;
  box-shadow: rgba(0, 0, 0, 0.4) 0 1px 5px;
}

.mobile-table-container table tbody tr td div.feature-container label {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  margin: 15px 0;
  border-bottom: 1px dashed;
  padding-bottom: 5px;
  text-align: center;
}
