.nav {
  width: 100%;
  padding: 4px 10px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.nav .brand {
  color: rgba(0, 0, 0, 0.3) !important;
}

.nav .nav-items {
  flex: 0.9;
}

.nav .nav-items .desktop-nav-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav .nav-items .desktop-nav-items .desktop-nav-item.left-item {
  flex: 0.7;
}

.nav .nav-items .burger-menu {
  display: none;
}

@media screen and (max-width: 768px) {
  .nav .nav-items .desktop-nav-items {
    display: none;
  }
  .nav .nav-items .burger-menu {
    display: flex !important;
    justify-content: flex-end;
  }
}
