.page-hostings {
  padding: 0 15px;
}
.page-hostings .form {
  padding: 30px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 4px;
  border-radius: 10px;
  width: 100%;
}

.page-hostings .form > label {
  margin: 10px 0;
  display: block;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.page-hostings .form .header {
  color: #000000b3;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-hostings .form .header svg {
  margin-right: 10px;
}

.page-hostings .form .ant-select {
  display: block;
}

.page-hostings .form .feature-input-items {
  max-height: 200px;
  overflow: scroll;
  padding: 30px 10px;
  margin-top: 20px;
}

.page-hostings .form .feature-input-items .feature-group-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 7px;
}
.page-hostings .form .feature-input-items .feature-group-item .ant-select {
  flex: 0.5;
}

.page-hostings .form .feature-input-items .feature-group-item .component,
.page-hostings .form .feature-input-items .feature-group-item .value {
  flex: 0.43;
}

.page-hostings .form .feature-input-items .feature-group-item .submit-btn {
  flex: 0.05;
}

.my-hostings .tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
}

.my-hostings .tools .actions {
  display: flex;
}

.my-hostings .tools .actions .select-btn {
}

.my-hostings .tools .actions .select-btn .ant-select {
  width: 90px;
}

.my-hostings .tools .actions .select-btn .ant-select-selector {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.my-hostings .tools .actions .select-btn > button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.my-hostings .tools .btn {
  display: flex;
  align-items: center;
}

.my-hostings .tools .add-btn .icon {
  margin-right: 5px;
}

.edit-modal .features-groups-lists {
  margin: 10px 0;
}

.edit-modal .features-groups-lists {
}

@media only screen and (max-width: 767px) {
  .page-hostings {
    padding: 0 5px;
  }
  .page-hostings .form {
    box-shadow: unset;
    padding: 0 15px;
    padding-top: 30px;
  }
  .page-hostings .form .feature-input-items {
    padding: 30px 0px;
  }
}
