.page-hostingTypes {
  padding: 0 5%;
}

.page-hostingTypes .tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
}

.page-hostingTypes .tools .btn {
  display: flex;
  align-items: center;
}
.page-hostingTypes .tools .add-btn .icon {
  margin-right: 5px;
}

.page-hostingTypes .tools .actions {
  display: flex;
}

.page-hostingTypes .form {
  margin-top: 20px;
  padding: 30px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 4px;
  border-radius: 10px;
  width: 100%;
}

.page-hostingTypes .form > label {
  margin: 10px 0;
  display: block;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.page-hostingTypes .form .header {
  color: #000000b3;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-hostingTypes .form .header svg {
  margin-right: 10px;
}

@media only screen and (max-width: 768px) {
  .page-hostingTypes {
    padding: 0;
  }
  .page-hostingTypes .form {
    box-shadow: unset;
  }
}
