.page-post {
  max-width: 90%;
  margin: 0 auto;
}

.post-metadata {
  margin: 20px 0;
  padding: 10px;
  border-bottom: 2px solid #bebebefa;
  padding-bottom: 20px;
}

.post-metadata .title {
  margin-bottom: 5px;
  color: rgb(33, 51, 67);
  font-size: 2.25rem;
}

.post-metadata .description {
  color: #616161;
  font-size: 16px;
  font-weight: 500;
  display: block;
  margin-bottom: 15px;
  display: block;
}

.post-metadata .date {
  font-style: italic;
  font-weight: bold;
  color: #0000008f;
  font-family: Silkscreen;
  margin-top: 10px;
  margin-right: 15px;
}

.post-metadata .topic {
  margin-right: 15px;
}

.post-metadata .author {
  text-decoration: underline;
  font-style: italic;
  text-wrap: nowrap;
}

.tiptap {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media screen and (max-width: 400px) {
  .page-post {
    max-width: 94%;
    margin: 0 auto;
  }
}
