.page-features {
  padding: 0 4%;
}
.page-features .form {
  margin-top: 20px;
  background: #fff;
  border-radius: 10px;
  width: 100%;
}

.page-features .form > label {
  margin: 10px 0;
  display: block;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
}

.page-features .form .header {
  color: #000000b3;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-features .form .header svg {
  margin-right: 10px;
}

.page-features .form .feature-input-items {
  max-height: 300px;
  overflow: scroll;
  padding: 5px 10px;
  margin-bottom: 10px;
  background-color: rgb(243, 243, 243);
}

.page-features .feature-input-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
}

.page-features .feature-input-group .name {
  flex: 0.3;
}

.page-features .feature-input-group .description {
  flex: 0.53;
}
.page-features .feature-input-group .data-type {
  flex: 0.15;
}
.page-features .feature-input-group .add-btn {
  display: flex;
  align-items: center;
}

.page-features .feature-input-group .add-btn .icon {
  margin-right: 5px;
}

.page-features .tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
}

.page-features .tools .actions {
  display: flex;
}

.page-features .tools .actions .select-btn {
}

.page-features .tools .actions .select-btn .ant-select {
  width: 90px;
}

.page-features .tools .actions .select-btn .ant-select-selector {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.page-features .tools .actions .select-btn > button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.page-features .tools .btn {
  display: flex;
  align-items: center;
}

.page-features .tools .add-btn .icon {
  margin-right: 5px;
}

.modal label {
  font-size: 16px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 5px;
  text-transform: capitalize;
  margin-top: 10px;
}

.modal label:not(.checkbox) {
  display: block;
}

.modal .data-type {
  display: block;
}

@media only screen and (max-width: 767px) {
  .page-features {
    padding: 0 5px;
  }
  .page-features .form {
    box-shadow: unset;
  }
}
