.page-newpost {
  padding: 20px;
  position: relative;
  height: 100%;
}

.page-newpost-button {
  float: right;
  margin: 5px 0;
}

.page-newpost-input {
  margin: 7px 0;
}

.page-newpost-input .label {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #000000b5;
  align-self: flex-start;
}

.page-newpost-input.title .titleInput {
  max-width: 350px;
  color: #000000a8;
  font-weight: 500;
  font-size: 16px;
}

.page-newpost-input.description .descriptionInput {
  max-width: 500px !important;
}

.page-newpost-input.image .imageInput {
  display: block;
  width: 300px;
  height: 200px;
  border: 2px dashed rgb(22, 119, 255);
  border-radius: 6px;
  box-sizing: content-box !important;
}

.page-newpost-input.image .imageButton {
  height: 100%;
  width: 100%;
  padding: 0;
}

.page-newpost-input.image .upload-instructions {
  text-align: center;
}
.page-newpost-input.image .upload-instructions .img-preview {
  width: 300px;
  height: 200px;
}
.page-newpost-input.image .upload-instructions .img-preview img {
  width: 300px;
  height: 200px;
}
.page-newpost-input.image .upload-instructions .icon {
  font-size: 90px;
}

.page-newpost-input.image .upload-instructions h4 {
  font-size: 14px;
  font-weight: 700;
}
.page-newpost-input.image .upload-instructions p {
  margin-top: 8px;
  font-size: 12px;
}
