.page-myprofile {
  height: auto !important;
  padding: 0 10%;
}
.page-myprofile .form {
  margin-top: 50px;
  padding: 30px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 4px;
  border-radius: 10px;
  width: 100%;
}

.page-myprofile .form .header {
  color: #000000b3;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-myprofile .form label {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  font-size: 16px;
  margin-top: 10px;
  margin-bottom: 6px;
  color: rgba(0, 0, 0, 0.7);
}

.page-myprofile .form .input {
  margin-bottom: 3px;
}

.page-myprofile .form .changepassword-btn {
  color: #1677ff;
  font-weight: 600;
  text-decoration: underline;
}

.page-myprofile .form .submit-btn {
  padding: 2.5px 25px;
  box-sizing: content-box;
  float: right;
  font-weight: 600;
  letter-spacing: 2px;
  margin-top: 10px;
}

@media only screen and (max-width: 768px) {
  .page-myprofile {
    width: 100%;
    padding: 0;
  }
  .page-myprofile .form {
    margin: 50px 0;
    box-shadow: unset;
  }
}
