.imageinput-container {
  display: contents;
  background-color: none;
  border: none;
}
.imageinput-button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 4px;
  border-radius: 5px;
}

.imageinput-button:hover {
  background-color: rgb(22, 119, 255);
  color: #fff;
}

.imageinput-button:focus {
  background-color: rgb(118, 171, 245);
  color: #fff;
}

.imageinput-input {
  display: none;
}
