.blogPosts-posts {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.blogPosts-posts .tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  padding: 10px;
  box-shadow: rgba(0, 0, 0, 0.3) -1px 1px 4px;
}

.blogPosts-posts .tools .actions {
  display: flex;
}

.blogPosts-posts .tools .btn {
  display: flex;
  align-items: center;
}

.blogPosts-posts .tools .add-btn .icon {
  margin-right: 5px;
}

.page-blogPosts-list {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  overflow: scroll;
}

.blogPosts-post-item {
  width: calc(33.3333% - 20px);
  margin-right: 10px;
  margin-bottom: 10px;
}

.blogPosts-post-item-img {
  height: 220px;
}

.blogPosts-post-item .card-details {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (max-width: 1204px) {
  .blogPosts-posts .page-blogPosts-list .blogPosts-post-item {
    width: calc(33.333% - 30px);
  }
}

@media screen and (max-width: 1338px) {
  .blogPosts-posts .page-blogPosts-list .blogPosts-post-item {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 767px) {
  .blogPosts-posts .page-blogPosts-list .blogPosts-post-item {
    width: calc(100% - 10px);
  }
}
