.by-comparing {
  height: 100%;
  padding: 0px 10px 10px 10px;
}

.by-comparing .tools-box-container {
  padding: 20px 10px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  margin: 10px 0;
  border-radius: 10px;
}

.by-comparing label {
  text-wrap: nowrap;
  margin-right: 5px;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  color: #00000085;
  margin-bottom: 7px;
  display: flex;
  align-items: center;
}
