.page-users {
  padding: 20px 30px;
  box-sizing: border-box;
}

.page-users .header {
  color: rgba(0, 0, 0, 0.7);
  font-family: Arial, Helvetica, sans-serif;
  color: #000000b3;
  font-size: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-users .header .icon {
  margin-right: 5px;
}

.page-users .tools {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0;
}

.page-users .tools .actions {
  display: flex;
}

.page-users .tools .actions .select-btn {
}

.page-users .tools .actions .select-btn .ant-select {
  width: 90px;
}

.page-users .tools .actions .select-btn .ant-select-selector {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.page-users .tools .actions .select-btn > button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.page-users .tools .btn {
  display: flex;
  align-items: center;
}

.page-users .tools .add-btn .icon {
  margin-right: 5px;
}

.modal-container {
  padding-top: 20px;
}

.modal-container label {
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 2px;
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(0, 0, 0, 0.7);
  display: block;
}

.modal-container .ant-select {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .page-users {
    padding: 20px 15px;
  }
}
