.app-.app-table table {
  width: 100%;
  overflow: scroll;

  background: #fafafa;
  user-select: none;
}

.table-container {
  overflow: scroll;
  display: flex;
  height: calc(100vh - 150px);
}

.app-table table {
  width: 100%;
  border-collapse: collapse;
  color: #5c5c5c;
  text-align: center;
}

.app-table tabletbody tr:nth-child(even) {
  background-color: #f3f4f9;
}

.app-table th {
  position: sticky;
  top: -1px;
  z-index: 1; /* Add a z-index to ensure the header is above the content */
}

.app-table th,
.app-table td {
  border-bottom: 1px solid #d3d2d2;
  padding: 10px;
  text-wrap: nowrap;
  border-collapse: separate;
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: 600;
  color: #000;
}

/* Add the following styles to remove unnecessary borders on the first row */
.app-table table thead tr th:first-child {
  border-top: none;
  border-left: none;
  position: sticky;
  top: -1px;
  left: -1px;
  background-color: #ff9900;
  z-index: 70;
  border: 1px solid #ff9900;
  box-sizing: content-box;
}

.app-table table tbody tr:hover td:first-child {
  background-color: #ff9900 !important;
  color: #fff;
}

.app-table table thead tr th:not(:first-child) {
  background-color: #283664;
  color: #fff;
  font-size: 18px;
}

.app-table table tbody tr td:hover {
  background: rgb(241, 239, 239);
}

.app-table table tbody tr td:first-child {
  background-color: #283664;
  position: sticky;
  left: 0;
  z-index: 60;
  color: #fff;
}

.app-table table tbody tr td:not(:first-child) {
  color: #283664;
  font-weight: 600;
  font-size: 18px;
}

.app-table table tbody tr td:first-child::after {
  content: "";
  width: 100%;
  height: 100%;
  border-bottom: 1px solid #354680;
  position: absolute;
  left: 0;
  top: 0;
}
