.radio-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.radio-container > label {
  margin-bottom: 7px;
  display: flex;
}

.radio-container label span {
  text-wrap: nowrap;
  margin-right: 5px;
  font-size: 14px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 600;
  color: #00000085;
}

.radio-container label .icon {
  color: #494949ca;
  font-weight: 600;
  cursor: help;
}
